* {
  box-sizing: border-box;
  border: 0;
  margin: 0;
  padding: 0;
}

:root {
  --bg: #000;
  --fg: #2471a3;
  --input: #aed6f1;
  --primary: #fff;
  --dur: 1s;
  font-size: calc(.833333vw + 13.3333px);
}

body, input {
  color: var(--fg);
  font: 1em / 1.5 Hind, sans-serif;
}

body {
  background-image: url("back.1ad61787.jpg");
  background-size: 100%;
}

form, input, .caret {
  margin: auto;
}

form {
  width: 100%;
  max-width: 17em;
  position: relative;
}

input, .caret {
  transition: all calc(var(--dur) * .5) linear;
  display: block;
}

input {
  caret-color: var(--primary);
  width: 2em;
  height: 2em;
  -webkit-appearance: none;
  appearance: none;
  background: none;
  border-radius: 50%;
  margin-top: 3em;
  box-shadow: inset 0 0 0 .25em;
}

input:hover {
  width: 3em;
  height: 3em;
}

input:hover + .caret {
  height: 3em;
}

input:focus, input:valid {
  background: var(--input);
  box-shadow: none;
  transition-duration: calc(var(--dur) * .25);
  transition-delay: calc(var(--dur) * .25);
  width: 100%;
  height: 3em;
  border-radius: .25em;
  padding: .75em 1em;
}

input:focus {
  animation: showCaret var(--dur) steps(1);
  outline: #0000;
}

input:focus + .caret, input:valid + .caret {
  animation: handleToCaret var(--dur) linear;
  width: 1px;
  height: 1.5em;
  background: none;
  transform: translate(0, -1em)rotate(-180deg)translate(7.5em, -.25em);
}

input::-webkit-search-decoration {
  -webkit-appearance: none;
}

label {
  color: #e3e4e8;
  width: 0;
  height: 0;
  position: absolute;
  overflow: hidden;
}

.caret {
  width: .25em;
  height: 2em;
  transform-origin: 50% 0;
  background: currentColor;
  border-radius: 0 0 .125em .125em;
  margin-bottom: -.6em;
  transform: translate(0, -1em)rotate(-45deg)translate(0, .875em);
}

@media (prefers-color-scheme: dark) {
  :root {
    --bg: #17181c;
    --fg: #e3e4e8;
    --input: #2e3138;
    --primary: #5583f6;
  }
}

@keyframes showCaret {
  from {
    caret-color: #0000;
  }

  to {
    caret-color: var(--primary);
  }
}

@keyframes handleToCaret {
  from {
    width: .25em;
    height: 1em;
    background: currentColor;
    transform: translate(0, -1em)rotate(-45deg)translate(0, .875em);
  }

  25% {
    width: .25em;
    height: 1em;
    background: currentColor;
    transform: translate(0, -1em)rotate(-180deg)translate(0, .875em);
  }

  50%, 62.5% {
    background: var(--primary);
    width: 1px;
    height: 1.5em;
    transform: translate(0, -1em)rotate(-180deg)translate(7.5em, 2.5em);
  }

  75%, 99% {
    background: var(--primary);
    width: 1px;
    height: 1.5em;
    transform: translate(0, -1em)rotate(-180deg)translate(7.5em, -.25em);
  }

  87.5% {
    background: var(--primary);
    width: 1px;
    height: 1.5em;
    transform: translate(0, -1em)rotate(-180deg)translate(7.5em, .125em);
  }

  to {
    width: 1px;
    height: 1.5em;
    background: none;
    transform: translate(0, -1em)rotate(-180deg)translate(7.5em, -.25em);
  }
}

.title {
  color: #aed6f1;
  text-align: center;
  margin-top: 40px;
}

.gallery {
  grid-column-gap: 8px;
  grid-row-gap: 8px;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  margin-top: 3em;
  display: grid;
}

.gallery img {
  max-width: 100%;
  border-radius: 8px;
  transition: all 1.5s;
  box-shadow: 0 0 16px #333;
}

.gallery img:hover {
  box-shadow: 0 0 32px #333;
}

.gallery .content {
  padding: 4px;
}

.gallery .gallery-item {
  cursor: pointer;
  transition: all .5s;
}

.gallery .gallery-item:hover {
  transform: scale(1.025);
}

@media (max-width: 600px) {
  .gallery {
    grid-template-columns: repeat(auto-fill, minmax(30%, 1fr));
  }
}

@media (max-width: 400px) {
  .gallery {
    grid-template-columns: repeat(auto-fill, minmax(50%, 1fr));
  }
}

.load-more {
  margin: 0 auto;
  padding: 50px;
  display: block;
}

